.App {
  text-align: center;
}

.App-logo {
  /* height: 40vmin; */
  width: 80px;
  height: 80px;
  pointer-events: none;
  mask-image: url("data:image/svg+xml;charset=utf-8,<svg width='80' height='80' viewBox='0 0 20 20' fill='none' xmlns='http://www.w3.org/2000/svg'><path d='M0 10C0 3.1 3.1 0 10 0C16.9 0 20 3.1 20 10C20 16.9 16.9 20 10 20C3.1 20 0 16.9 0 10Z' fill='black'/></svg>");

}

